import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './RssItem.css';
import Utils from '../component/Utils'
import EventBus from '../component/EventBus'
import { Checkbox, Button  } from 'antd';

export default class RssItem extends Component {

	static propTypes = {
		onSelChange: PropTypes.func
	}

	constructor(props) {
		super(props);
		this.state =  {
		}
	}

	componentDidMount() {
	}

	_onSelChange = (e)=> {
		if (this.props.onSelChange === undefined)
			return;
		this.props.onSelChange(this.props.fileInfo.idx, e.target.checked);
	}

	_renderRssInfo = ()=> {
		if (this.props.rssInfo.last_pub.length == 0) {
			return (
				<div className="rssinfo">
				无最新发布
				</div>
			);
		}
		return (
			<div className="rssinfo">
			{this.props.rssInfo.last_file} 发布于： {this.props.rssInfo.last_pub}
			</div>
		);
	}

	render() {
		return (
			<div className="rssitem" onClick={this._onSelChange}>
				<div className="nameInfo">
					[{this.props.rssInfo.cate}]{this.props.rssInfo.name}
				</div>
				{this._renderRssInfo()}
			</div>
		)

	}
}



