import LinkNode from './LinkNode'

export default class LinkedList {

	constructor() {
		this.header = null;
		this.tail = null;
		this.nodeCnt = 0;
	}

	add2Tail = (linknode) => {
		if (linknode.prev != null || linknode.next != null || linknode == this.header)
			return false;
		if (this.tail == null) {
			this.tail = this.header = linknode;
		} else {
			this.tail.next = linknode;
			linknode.prev = this.tail;
			this.tail = linknode;
		}
		this.nodeCnt++;
		return true;
	}

	addAt = (idx, linknode) => {
		if (linknode.next != null || linknode.prev != null)
			return false;
		if (idx < 0 || idx >= this.nodeCnt)
			return false;
		var node = this.header;
		while (idx > 0) {
			idx--;
			node = node.next;
		}

		linknode.next = node;
		if (node != null) {
			linknode.prev = node.prev;
			node.prev = linknode;
		} else {
			this.tail = this.header = linknode;
			linknode.prev = null;
		}
		this.nodeCnt++;
		return true;
	}

	addBefore = (linknode, newlinknode) => {
		var prenode = linknode.prev;
		newlinknode.next = linknode;
		linknode.pre = newlinknode;
		newlinknode.prev = prenode;

		if (prenode == null) {
			this.header = newlinknode;
		} else {
			prenode.next = newlinknode;
		}
		this.nodeCnt++;
	}

	replaceAt = (idx, linknode) => {
		if (linknode.next != null || linknode.prev != null)
			return false;
		if (idx < 0 || idx >= this.nodeCnt)
			return false;
		var node = this.header;
		while (idx > 0) {
			idx--;
			node = node.next;
		}

		linknode.next = node.next;
		linknode.prev = node.prev;
		if (node.prev == null)
			this.header = linknode;
		if (node == this.tail)
			this.tail = linknode;
		return true;
	}

	add2Head = (linknode) => {
		if (linknode.prev != null || linknode.next != null || linknode == this.header)
			return false;
		linknode.next = linknode;
		if (this.header == null) {
			this.tail = linknode;
		} else {
			this.header.next = linknode;
		}
		this.header = linknode;
		this.nodeCnt++;
		return true;
	}

	remove = (node) => {
		if (node.prev == null && node.next == null && node != this.header)
			return false;
		if (node.prev != null)
			node.prev.next = node.next;
		if (node.next != null)
			node.next.prev = node.prev;
		if (this.header == node)
			this.header = node.next;
		if (this.tail == node)
			this.tail = node.prev;

		node.prev = node.next = null;
		this.nodeCnt--;
		return true;
	}

	removeHead = () => {
		var node = null;
		if (this.nodeCnt == 0)
			return null;
		node = this.header;
		this.header = this.header.next;
		if (this.tail == node)
			this.tail = null;
		if (this.header != null)
			this.header.prev = null;
		node.prev = node.next = null;
		this.nodeCnt--;
		return node;
	}

	removeTail = () => {
		var node = null;
		if (this.nodeCnt == 0)
			return null;

		node = this.tail;
		this.tail = node.prev;
		if (this.tail != null)
			this.tail.next = null;
		else
			this.header = null;
		node.prev = null;
		this.nodeCnt--;
		return node;
	};

	getHead = () => {
		return this.header;
	};

	getTail = () => {
		return this.tail;
	};

	clear = () => {
		while (this.removeHead() != null) { }
	};

	size = () => {
		return this.nodeCnt;
	};

	removeAt = (idx) => {
		if (idx < 0 || idx >= this.nodeCnt)
			return false;
		var node = this.header;
		while (idx > 0) {
			idx--;
			node = node.next;
		}

		if (node.prev != null)
			node.prev.next = node.next;

		if (node.next != null)
			node.next.prev = node.prev;

		if (this.header == node)
			this.header = node.next;
		if (this.tail == node)
			this.tail = node.prev;

		node.prev = node.next = null;
		this.nodeCnt--;
		return true;
	};

	getAt = (idx) => {
		if (idx < 0 || idx >= this.nodeCnt)
			return null;
		var node = this.header;
		while (idx > 0) {
			idx--;
			node = node.next;
		}
		return node;
	};

	find = (linknode) => {
		var node = this.header;
		while (node != null) {
			if (node == linknode)
				return true;
			node = node.next;
		}
		return false;
	};

	each = (cb) => {
		var node = this.header;
		var thisnode = null;
		while (node != null) {
			thisnode = node;
			node = node.next;
			if (!cb(thisnode))
				break;
		}
	}
}


