import LinkNode from './LinkNode'
import LinkedList from './LinkedList'

class EventBus {
	static myInstance = null;

	static getInstance() {
        if (EventBus.myInstance == null) {
            EventBus.myInstance = new EventBus();
        }
        return EventBus.myInstance;
	}

	constructor() {
		this.eventMap = {};
	}
	
	addEventListener =(eventName, cb)=> {
		var cbList = this.eventMap[eventName];
		if (cbList == null || cbList == undefined) {
			cbList = new LinkedList();
			this.eventMap[eventName] = cbList;
		}
		var listenerHandle = {};
		listenerHandle.eventName = eventName;
		listenerHandle.cb = cb;
		listenerHandle.node = new LinkNode(listenerHandle);
		cbList.add2Tail(listenerHandle.node);
		return listenerHandle;
	}

	removeEventListener = (listenerHandle)=> {
		var cbList = this.eventMap[listenerHandle.eventName];
		if (cbList == null || cbList == undefined)
			return;
		cbList.remove(listenerHandle.node);
		if (cbList.size() == 0) {
			this.eventMap[listenerHandle.eventName] = undefined;
		}
	}

	emitEvent = (eventName, param) => {
		//console.log('emit:' + eventName);
		var cbList = this.eventMap[eventName];
		if (cbList == null || cbList == undefined)
			return;
		cbList.each(function(node) {
			//console.log('cb');
			var listenerHandle = node.node;
			listenerHandle.cb(param);
		});
	}
}

var eventBus = EventBus.getInstance();

export default eventBus


