import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Icon, Tooltip } from 'antd';
import './SysButton.css';

export default class SysButton extends Component {

	static propTypes = {
		name: PropTypes.string.isRequired,
		fontSize: PropTypes.number,
		onClick: PropTypes.func
	}

	static defaultProps = {
		fontSize: 16
	}

	constructor(props) {
		super(props);
		this.state = {
			iconcolor: '#f0f0f0'
		}
	}

	componentDidMount() {

	}

	_onClick = () => {
		if (this.props.onClick !== undefined) {
			this.props.onClick();
		}
	}

	_onMouseDown = () => {
		this.setState({ iconcolor: '#cccccc' });
	}

	_onMouseUp = () => {
		this.setState({ iconcolor: '#f0f0f0' });
	}

	render() {
		return (
			<Tooltip placement="bottom" title={this.props.tip}>
				<div
					className="sysbcontainer"
					onMouseDown={this._onMouseDown}
					onMouseUp={this._onMouseUp}
					onClick={this._onClick}>
					<Icon type={this.props.name} style={{ color: this.state.iconcolor, fontSize: this.props.fontSize }} className="ibicon" />
				</div>
			</Tooltip>
		)

	}
}



