import React, { Component } from 'react';
import Constants from '../constants/Constants'
import './TaskList.css';
import TaskElement from './TaskElement'
import IconButton from '../component/IconButton'
import InfiniteScroll from 'react-infinite-scroller';
import { List, Spin } from 'antd';
import HttpUtil from '../component/HttpUtil';
import EventBus from '../component/EventBus'
import TaskCommand from './TaskCommand'

export default class TaskList extends Component {

	state = {
		windowHeight: window.innerHeight
	};

	constructor(props) {
		super(props);
		this.state = {
			data: [],
			loading: false,
			listHeight: window.innerHeight - Constants.HEADER_HEIGHT - 50

		}
		this.taskMap = {};
		console.log("create task list");
		this.queryHandle = null;
		this.statHandle = null;
		this.statTimer = null;
	}

	componentDidMount() {
		window.addEventListener('resize', this.handleResize);
		this._queryTask();
		this.statTimer = setInterval(() => {
			this._queryTaskStat();
		}, 1000 * this.props.taskType);

	}

	componentWillUnmount() {
		if (this.statTimer != null) {
			clearInterval(this.statTimer);
			this.statTimer = null;
		}
		if (this.queryHandle != null) {
			this.queryHandle.abort();
			this.queryHandle = null;
		}
		if (this.statHandle != null) {
			this.statHandle.abort();
			this.statHandle = null;
		}
	}

	handleResize = (e) => {
		this.setState({ listHeight: window.innerHeight - Constants.HEADER_HEIGHT - 50 })
	};

	_queryTask = () => {
		if (this.queryHandle != null) {
			this.queryHandle.abort();
			this.queryHandle = null;
		}
		this.setState({ loading: true });
		var url = "";
		if (this.props.taskType == 1) {
			url = "api/workingtask"
		} else {
			url = "api/finishedtask"
		}
		this.queryHandle = HttpUtil.get(url, (result, info) => {
			this.queryHandle = null;
			if (!result) {
				this.setState({ loading: false });
				return;
			}
			try {
				var taskjson = JSON.parse(info);
				if (taskjson.code == 600) {
					EventBus.emitEvent("unlock", null);
					return;
				}
				if (taskjson.code != 200) {
					this.setState({ loading: false });
					return;
				}
				var tasks = taskjson.tasks;
				for (var i = 0; i < tasks.length; i++) {
					tasks[i].index = i;
					this.taskMap[tasks[i].file_id] = tasks[i];
				}
				this.setState({ loading: false, data: tasks });
			} catch (err) {
				console.log(err);
			}
		}, 15000);
	}

	_queryTaskStat = () => {
		if (this.statHandle != null) {
			return;
		}
		var url = "";
		if (this.props.taskType == 1) {
			url = "api/workingtask"
		} else {
			url = "api/finishedtask"
		}
		this.statHandle = HttpUtil.get(url, (result, text) => {
			this.statHandle = null;
			if (!result)
				return;
			try {
				var taskjson = JSON.parse(text);
				if (taskjson.code == 600) {
					EventBus.emitEvent("unlock", null);
					return;
				}
				if (taskjson.code != 200) {
					return;
				}
				var newmap = {};
				var tasks = taskjson.tasks;
				for (var i = 0; i < tasks.length; i++) {
					tasks[i].index = i;
					var pretask = this.taskMap[tasks[i].file_id];
					if (pretask != undefined) {
						tasks[i].selected = pretask.selected;
					}
					newmap[tasks[i].file_id] = tasks[i];
				}
				this.taskMap = newmap;
				this.setState({ data: tasks });
			} catch (err) {
			}
		}, 5000);
	}

	_onClickItem = (index) => {
		var hasChange = false;
		var newdata = this.state.data;
		//var newmap = {};
		for (var i = 0; i < newdata.length; i++) {
			if (i == index) {
				if (newdata[i].selected != true) {
					newdata[i].selected = true;
					hasChange = true;
				}
			} else if (newdata[i].selected == true) {
				newdata[i].selected = false;
				hasChange = true;
			}
			//newmap[newdata[i].file_id] = newdata[i];
		}

		if (hasChange) {
			//this.taskMap = newmap;
			this.setState({ data: newdata });
		}
	}

	_renderTaskItem = (item) => {
		return (
			<TaskElement task={item} taskType={this.props.taskType} selected={false} onItemClick={this._onClickItem}></TaskElement>
		);
	};

	_loadMoreList() { }

	_readerControls = () => {
		if (this.state.loading) {
			return (
				<div className="plloading">
					<Spin size="large" className="plspin" />
				</div>
			);
		}

		return (
			<div style={{ overflow: "auto", width: "100%", height: this.state.listHeight }}>
				<InfiniteScroll
					initialLoad={false}
					pageStart={0}
					hasMore={false}
					loadMore={this._loadMoreList}
					useWindow={false}>
					<List
						dataSource={this.state.data}
						renderItem={this._renderTaskItem}
					>
					</List>
				</InfiniteScroll>
			</div>
		);
	}

	_selectAll = () => {
		var newdata = this.state.data;
		for (var i = 0; i < newdata.length; i++) {
			newdata[i].selected = true;
		}
		this.setState({ data: newdata });
	}

	_reverseSelectAll = () => {
		var newdata = this.state.data;
		for (var i = 0; i < newdata.length; i++) {
			if (newdata[i].selected == undefined) {
				newdata[i].selected = true;
			} else {
				newdata[i].selected = !newdata[i].selected;
			}
		}
		this.setState({ data: newdata });
	}

	_readerToolBarIcon = () => {
		if (this.props.taskType == 1) {
			return (
				<div>
					<div className="toolbarIcon" onClick={this._resumeAll}>
						<IconButton name="download" tip="全部开始"  ></IconButton>
					</div>
					<div className="toolbarIcon" onClick={this._pauseAll}>
						<IconButton name="pause" tip="全部暂停"  ></IconButton>
					</div>
				</div>
			);
		}
	}

	_deleteFiles = () => {
		var params = {};
		params.deleteFile = (this.props.taskType == 1);
		params.ids = [];
		for (var i = 0; i < this.state.data.length; i++) {
			if (this.state.data[i].selected == true) {
				params.ids[params.ids.length] = this.state.data[i].file_id;
			}
		}

		if (params.ids.length == 0)
			return;

		EventBus.emitEvent("deltask", params);
	}
	
	_pauseAll =() => {
		var ids = [];
		for (var i = 0; i < this.state.data.length; i++) {
			ids[ids.length] = this.state.data[i].file_id;
		}
		if (ids.length == 0)
			return;
		TaskCommand.pause(ids);
	}

	_resumeAll =() => {
		var ids = [];
		for (var i = 0; i < this.state.data.length; i++) {
			ids[ids.length] = this.state.data[i].file_id;
		}
		if (ids.length == 0)
			return;
		TaskCommand.resume(ids);
	}

	render() {
		return (
			<div>
				<div className="pltoolbar">
					<div className="toolbarIcon">
						<IconButton name="reload" tip="刷新" onClick={this._queryTask} ></IconButton>
					</div>
					<div className="toolbarIcon">
						<IconButton name="check-square-o" tip="全选" onClick={this._selectAll} ></IconButton>
					</div>
					<div className="toolbarIcon">
						<IconButton name="check-square" tip="反选" onClick={this._reverseSelectAll}></IconButton>
					</div>
					{this._readerToolBarIcon()}
					<div className="toolbarIcon">
						<IconButton name="delete" tip="删除选中" onClick={this._deleteFiles}></IconButton>
					</div>

				</div>
				{this._readerControls()}
			</div>
		)
	}
}



