import React, { Component } from 'react';
import Constants from './constants/Constants'
import './App.css';
import Publist from './publist/Publist'
import RssList from './rss/RssList'
import WorkingTask from './task/WorkingTask'
import FinishedTask from './task/FinishedTask'
import SysButton from './component/SysButton'
import EventBus from './component/EventBus'
import { BrowserRouter as Router, Route, Switch, Redirect, NavLink } from 'react-router-dom'
import HttpUtil from './component/HttpUtil';
import TaskCommand from './task/TaskCommand';
import { Layout, Menu, Icon, Modal, message, Checkbox, Input, Popover, Button, Radio } from 'antd';
import Utils from './component/Utils';
const { TextArea } = Input;
const { Header, Content, Sider } = Layout;
const RadioGroup = Radio.Group;

export default class App extends Component {

	constructor(props) {
		super(props)
		this.state = {
			windowHeight: window.innerHeight,
			confirmVisible: false,
			deleteFile: false,
			loginVisible: false,
			loginUname: '',
			loginPasswd: '',
			isLoging: false,
			loginTip: ' ',
			userInfoVisible: false,
			totalUpload: '',
			settingVisible: false,
			loadingSetting: false,
			setting: {},
			speedUpTxt: '',
			speedDownTxt: '',
			buldDlVisible: false,
			buldUrls: '',
			unlockVisible: false,
			unlockPasswd: ''
		};

		this.userInfo = null;
		this.userInfoHandle = null;
		this.queryUploadHandle = null;
		this.settingHandle = null;
		this.statHandle = null;
	}

	componentDidMount() {
		console.log("create app");
		window.addEventListener('resize', this.handleResize);
		EventBus.addEventListener("newtask", (param) => {
			this._addTask(param);
		});

		EventBus.addEventListener("deltask", (param) => {
			this.setState({
				confirmVisible: true,
				deleteFile: param.deleteFile
			});
			this.delTaskIds = param.ids;
		});

		setInterval(()=> {
			this._queryStat()
		}, 1000);

		EventBus.addEventListener("unlock", (param) => {
			if (this.state.unlockVisible)
				return
			this.setState({unlockVisible: true, unlockPasswd: ''});
		});
	}

	_queryStat = ()=> {
		if (this.statHandle != null) {
			return;
		}
		this.statHandle = HttpUtil.get("api/stat", (result, text) => {
			this.statHandle = null;
			if (!result) {
				this.setState({speedTxt: ''});
				return;
			}
			try {
				var speed = JSON.parse(text);
				if (speed.code == 600) {
					EventBus.emitEvent("unlock", null);
					return;
				}
				var downTxt = "下载：" + Utils.formatSize(speed.dlspeed) + "/s"
				var upTxt = "上传：" + Utils.formatSize(speed.upspeed) + "/s"
				this.setState({speedDownTxt: downTxt, speedUpTxt: upTxt});
			} catch (err) {
				this.setState({speedTxt: ''});
			}
		}, 5000);
	}

	_queryUserInfo = (cb) => {
		this.userInfoHandle = HttpUtil.get('api/getuserinfo', (result, text) => {
			this.userInfoHandle = null;
			if (!result) {
				return;
			}
			try {
				var info = JSON.parse(text);
				if (info.code == 200) {
					this.userInfo = info;
					if (cb != undefined)
						cb();
				}
			} catch (err) {
			}
		}, 15000);
	}

	_addTask = (param) => {
		HttpUtil.post("api/addtask", "tasks=" + encodeURIComponent(JSON.stringify(param)), (result, text) => {
			message.info("成功添加" + param.length + "个任务");
		}, 10000);
	}

	handleResize = (e) => {
		this.setState({ windowHeight: window.innerHeight })
	};

	_handleOk = (e) => {
		this.setState({
			confirmVisible: false,
		});
		TaskCommand.delete(this.delTaskIds, this.state.deleteFile);
	}

	_handleCancel = (e) => {
		this.setState({
			confirmVisible: false,
		});
	}

	_onDelFileChange = (e) => {
		this.setState({ deleteFile: e.target.checked });
	}

	_showUserInfo = () => {
		if (this.userInfo == null)
			return;

		if (this.userInfo.logined) {
			this._queryUpload();
			this.setState({ loginTip: ' ', isLoging: false, loginVisible: false, userInfoVisible: true })
		} else {
			this.setState({ loginTip: ' ', loginVisible: true, loginUname: this.userInfo.uname, loginPasswd: this.userInfo.passwd })
		}
	}

	_onClickUser = () => {
		if (this._showUserInfo())
			return;
		this._queryUserInfo(() => {
			this._showUserInfo();
		});
	}

	_handleLoginOk = () => {
		if (this.state.isLoging)
			return;
		if (this.state.loginPasswd.length < 2 || this.state.loginUname.length < 2)
			return;
		this.setState({ isLoging: true, loginTip: '正在登录.....' })
		var url = 'api/login?uname=' + encodeURIComponent(this.state.loginUname) + '&passwd=' + encodeURIComponent(this.state.loginPasswd)
		HttpUtil.get(url, (result, text) => {
			if (!result) {
				this.setState({ isLoging: false, loginTip: "网络错误,稍后重试" });
				return;
			}
			try {
				var info = JSON.parse(text);
				if (info.code != 200 || info.logined == false) {
					this.setState({ isLoging: false, loginTip: "登录错误，请检查用户名和密码！" });
					return;
				}
				this.userInfo = info;
				this._showUserInfo();
			} catch (err) {
				this.setState({ isLoging: false, loginTip: "网络错误,稍后重试" });
				return;
			}
		}, 15000)
	}

	_handleLoginCancel = () => {
		this.setState({ loginVisible: false })
	}

	_onChangeUname = (e) => {
		this.state.loginUname = e.target.value
	}

	_onChangePasswd = (e) => {
		this.state.loginPasswd = e.target.value
	}

	_renderUserInfo = () => {
		if (this.userInfo == null) {
			return (
				<div className="uinfopanel"></div>
			);
		}
		return (
			<div className="uinfopanel">
				<img className="uimg" src={this.userInfo.userpic} />
				<div className="utext1">{this.userInfo.nickname}</div>
				<div className="utext2">{this.userInfo.group_name}</div>
				<div className="utext3">{this.state.totalUpload}</div>
				<div className="utext4">（每小时更新一次数据）</div>
				<div className="ubtn"><Button type="primary" onClick={this._clickLoginOut}>退出登录</Button></div>
			</div>
		);
	}

	_onUserPanelHide = (visible) => {
		console.log("visible:" + visible);
		this.setState({ userInfoVisible: visible });
	}

	_queryUpload = () => {
		if (this.queryUploadHandle != null)
			return;
		this.queryUploadHandle = HttpUtil.get('api/myupload', (result, text) => {
			this.queryUploadHandle = null;
			if (!result)
				return;

			try {
				var info = JSON.parse(text);
				if (info.status != 1)
					return;
				var size = parseInt(info.data.total_size);
				console.log(text);
				size = "总上传：" + Utils.formatSize(size);
				this.setState({ totalUpload: size });
			} catch (err) {

			}

		}, 10000);
	}

	_clickLoginOut = () => {
		HttpUtil.get("api/logout", (result, text) => {
			if (!result)
				return;
			this.userInfo = null;
			this.setState({ loginTip: ' ', isLoging: false, loginVisible: false, userInfoVisible: false })
		}, 10000);
	}

	_handleSettingOk = () => {
		this.setState({ settingVisible: false });
		var url = 'api/savesetting?setting=' + encodeURIComponent(JSON.stringify(this.state.setting))
		HttpUtil.get(url, (result, text) => {}, 10000);
	}

	_handleSettingCancel = () => {
		this.setState({ settingVisible: false });
	}

	_onClickSetting = () => {
		this.setState({ settingVisible: true, loadingSetting: true });
		this._querySetting()
	}

	_querySetting = ()=> {
		if (this.settingHandle != null) {
			this.settingHandle.abort();
			this.settingHandle = null;
		}
		this.setState({loadingSetting: true})
		this.settingHandle = HttpUtil.get("api/getsetting", (result, text) => {
			this.settingHandle = null;
			if (!result) {
				return;
			}
			try {
				var info = JSON.parse(text);
				if (info.code == 600) {
					EventBus.emitEvent("unlock", null);
					return;
				}
				this.setState({loadingSetting: false, setting: info})
			} catch (err) {
				console.log(err);
			}
		}, 15000);
	}

	_onChangeSavePath =(e)=> {
		var newsetting = this.state.setting;
		newsetting.save_path = e.target.value;
		this.setState({setting: newsetting});
	}

	_onChangeTaskCnt =(e) => {
		var newsetting = this.state.setting;
		newsetting.task_cnt = parseInt(e.target.value);
		this.setState({setting: newsetting});
	}

	_onChangeDownSpeed =(e) => {
		var newsetting = this.state.setting;
		newsetting.down_speed = parseInt(e.target.value);
		this.setState({setting: newsetting});
	}

	_onChangeUpSpeed =(e) => {
		var newsetting = this.state.setting;
		newsetting.up_speed = parseInt(e.target.value);
		this.setState({setting: newsetting});
	}

	_onChangeSpeedLimit =(e) => {
		var newsetting = this.state.setting;
		newsetting.speed_limit = e.target.checked;
		this.setState({setting: newsetting});
	}

	_onAutoDlChange = (e) => {
		var newsetting = this.state.setting;
		newsetting.auto_dl = parseInt(e.target.value);
		this.setState({setting: newsetting});
	}

	_onChangeLockPasswd =(e) => {
		var newsetting = this.state.setting;
		newsetting.lock_passwd = e.target.value;
		this.setState({setting: newsetting});
	}

	_renderSetting = () => {
		if (this.state.loadingSetting) {
			return (<div>正在加载配置...</div>);
		} else {
			return (
				<div>
					<div className="setline">
						<Input addonBefore="存储路径：" defaultValue={this.state.setting.save_path} onChange={this._onChangeSavePath} />
					</div>
					<div className="setline">
						<Input addonBefore="同时下载任务数:" style={{ width: "200px" }} defaultValue={this.state.setting.task_cnt}  onChange={this._onChangeTaskCnt} />
					</div>
					<div className="setline" style={{ lineHeight: "48px" }}>
						<Checkbox checked={this.state.setting.speed_limit} onChange={this._onChangeSpeedLimit}>限制速度（填0不限速)</Checkbox>
					</div>
					<div className="setline">
						<Input addonBefore="下载速度:" style={{ width: "200px" }} addonAfter="KB/s" defaultValue={this.state.setting.down_speed}   onChange={this._onChangeDownSpeed} />
						<Input addonBefore="上传速度:" style={{ width: "200px", marginLeft: "12px" }} addonAfter="KB/s" defaultValue={this.state.setting.up_speed}    onChange={this._onChangeUpSpeed}/>
					</div>
					<div className="setline" style={{ marginTop: "12px" }}>
						<div style={{ width: "80px", float: "left" }}>自动下载：</div>
						<div style={{ width: "300px", float: "left" }}>
							<RadioGroup onChange={this._onAutoDlChange} value={this.state.setting.auto_dl}>
								<Radio value={1}>不下载</Radio>
								<Radio value={2}>只下载订阅</Radio>
								<Radio value={3}>全部下载</Radio>
							</RadioGroup>
						</div>
					</div>
					<div className="setline">
						<Input addonBefore="解锁密码:" style={{ width: "300px" }} defaultValue={this.state.setting.lock_passwd}  onChange={this._onChangeLockPasswd} />
					</div>
				</div>
			);
		}
	}

	_handlebuldDlOk = ()=> {
		this.setState({buldDlVisible: false});
		var urls = this.state.buldUrls.split("\n");
		EventBus.emitEvent("newtask", urls);
		this.setState({buldUrls: ''});
	}

	_handlebuldDlCancel = ()=> {
		this.setState({buldDlVisible: false});
	}

	_onClickBuld = ()=> {
		this.setState({buldDlVisible: true});
	}

	_onBuldTextChanged =(e)=> {
		this.setState({buldUrls: e.target.value});
	}

	_handleUnlockOk =()=> {
		if (this.state.unlockPasswd.length == 0)
			return;
		var url = 'api/unlock?passwd=' + encodeURIComponent(this.state.unlockPasswd)
		HttpUtil.get(url, (result, text)=> {
			if (!result) {
				return
			}
			try {
				var info = JSON.parse(text);
				if (info.code == 200) {
					window.location.href = '/';
				}
			} catch (err) {
				console.log(err);
			}
		}, 10000);
		//this.setState({unlockVisible: true});
	}

	_handlebUnlockCancel =()=> {
		//this.setState({unlockVisible: false});
	}

	_onUnlockPasswdChange =(e)=> {
		this.setState({unlockPasswd: e.target.value});
	}

	_onClickLock =()=> {
		HttpUtil.get('api/lock', (result, text) => {}, 10000);
	}

	render() {
		const navHeight = this.state.windowHeight - Constants.HEADER_HEIGHT;
		return (
			<div>
				<Router>
					<Layout>
						<Header className="titleLine">
							<div className="logodiv"><img src="images/logo.png" /></div>
							<div className="iconDiv">
								<SysButton name="setting" tip="设置" fontSize={24} onClick={this._onClickSetting} />
							</div>
							<div className="iconDiv">
								<SysButton name="user" tip="用户" fontSize={24} onClick={this._onClickUser} />
								<Popover placement="bottomRight" trigger="click" title=""
									onVisibleChange={this._onUserPanelHide} content={this._renderUserInfo()}
									visible={this.state.userInfoVisible} defaultVisible={false}>
									<div></div>
								</Popover>
							</div>
							<div className="iconDiv">
								<SysButton name="download" tip="批量下载" fontSize={24} onClick={this._onClickBuld} />
							</div>
							<div className="iconDiv">
								<SysButton name="lock" tip="锁定" fontSize={24} onClick={this._onClickLock} />
							</div>
						</Header>
						<Layout>
							<Sider style={{ height: navHeight }} className="nav">
								<Menu mode="inline" defaultSelectedKeys={['downloading']} inlineIndent="40">
									<Menu.Item key="downloading">
										<NavLink to="/downloading"><Icon type="inbox" style={{ fontSize: 18 }} />正在下载</NavLink>
									</Menu.Item>
									<Menu.Item key="finished">
										<NavLink to="/finished"><Icon type="desktop" style={{ fontSize: 18 }} />下载完成</NavLink>
									</Menu.Item>
									<Menu.Item key="publist">
										<NavLink to="/publist"><Icon type="mail" style={{ fontSize: 18 }} />最新更新</NavLink>
									</Menu.Item>
									<Menu.Item key="collection">
										<NavLink to="/collection"><Icon type="pie-chart" style={{ fontSize: 18 }} />我的收藏</NavLink>
									</Menu.Item>
								</Menu>
								<div className="speeddiv">
									<div className="speed">
										{this.state.speedDownTxt}
									</div>
									<div className="speed">
										{this.state.speedUpTxt}
									</div>
								</div>
							</Sider>
							<Content style={{ height: navHeight }}>
								<Switch>
									<Route path="/downloading" component={WorkingTask} />
									<Route path="/finished" component={FinishedTask} />
									<Route path="/publist" component={Publist} />
									<Route path="/collection" component={RssList} />
									<Route path="/" component={WorkingTask} />

								</Switch>
							</Content>
						</Layout>
					</Layout>
				</Router>
				<Modal
					title="确认删除"
					visible={this.state.confirmVisible}
					onOk={this._handleOk}
					onCancel={this._handleCancel}
				>
					<Checkbox checked={this.state.deleteFile} onChange={this._onDelFileChange}>同时删除文件</Checkbox>
				</Modal>
				<Modal
					title="用户登录"
					visible={this.state.loginVisible}
					onOk={this._handleLoginOk}
					onCancel={this._handleLoginCancel}
					cancelText="取消"
					okText="登录">
					<div><Input placeholder="用户名" defaultValue={this.state.loginUname} onChange={this._onChangeUname} /></div>
					<div style={{ marginTop: "15px" }}><Input placeholder="密码" type="password" defaultValue={this.state.loginPasswd} onChange={this._onChangePasswd} /></div>
					<div style={{ marginTop: "15px" }}>{this.state.loginTip}</div>
				</Modal>

				<Modal
					title="设置"
					visible={this.state.settingVisible}
					onOk={this._handleSettingOk}
					onCancel={this._handleSettingCancel}
					cancelText="取消"
					okText="确定">
					{this._renderSetting()}
				</Modal>

				<Modal
					title="批量下载"
					visible={this.state.buldDlVisible}
					onOk={this._handlebuldDlOk}
					onCancel={this._handlebuldDlCancel}
					cancelText="取消"
					okText="确定">
					<TextArea placeholder="粘贴链接" defaultValue={this.state.buldUrls} onChange={this._onBuldTextChanged} autosize={{ minRows: 16, maxRows: 16 }} />
				</Modal>

				<Modal
					title="操作解锁"
					visible={this.state.unlockVisible}
					onOk={this._handleUnlockOk}
					onCancel={this._handlebUnlockCancel}
					cancelText="取消"
					closable={false}
					okText="确定">
					<Input placeholder="解锁密码" onChange={this._onUnlockPasswdChange} defaultValue={this.state.unlockPasswd} />
				</Modal>
			</div>
		);
	}
}



