import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './PublistItem.css';
import Utils from '../component/Utils'
import EventBus from '../component/EventBus'
import {CopyToClipboard} from 'react-copy-to-clipboard';
import { Checkbox, Button, message  } from 'antd';

export default class PublistItem extends Component {

	static propTypes = {
		onSelChange: PropTypes.func
	}

	constructor(props) {
		super(props);
		this.state =  {
			divWidth: 100
		}
	}

	componentDidMount() {
		this.setState({divWidth : this.refs.topDiv.offsetWidth - 520});
		window.addEventListener('resize', this.handleResize);
	}

	handleResize = (e) => {
		this.setState({divWidth : this.refs.topDiv.offsetWidth - 520});
	};

	_onSelChange = (e)=> {
		if (this.props.onSelChange === undefined)
			return;
		this.props.onSelChange(this.props.fileInfo.idx, e.target.checked);
	}

	_onDownload = ()=> {
		var param = [];
		var url = "yyets://H=" + this.props.fileInfo.fileid + "|S=" + this.props.fileInfo.file_size + "|N=" + this.props.fileInfo.file_name + "|";
		param[0] = url;
		EventBus.emitEvent("newtask", param);
	}

	_onCopyUrl= () =>{
		message.info("已复制下载链接");
	}

	_getUrl = ()=> {
		var url = "yyets://H=" + this.props.fileInfo.fileid + "|S=" + this.props.fileInfo.file_size + "|N=" + this.props.fileInfo.file_name + "|";
		return url;
	}

	render() {
		return (
			<div ref="topDiv" className="plem plemunfocus">
				<div className="plfl" style={{width: "30px"}}>
					<Checkbox 
					checked={this.props.fileInfo.selected === true}
					onChange={this._onSelChange} />
				</div>
				<div className="plfl plfont" style={{width: this.state.divWidth, overflow: "hidden"}}>{this.props.fileInfo.file_name}</div>
				<div className="plfl plfont" style={{width: "155px", paddingLeft: "6px"}}>{this.props.fileInfo.create_time}</div>
				<div className="plfl plfont" style={{width: "90px", paddingLeft: "10px"}}>{Utils.formatSize(this.props.fileInfo.file_size)}</div>
				<div className="plfl" style={{width: "110px", paddingLeft: "10px"}}><Button onClick={this._onDownload}>立即下载</Button></div>
				<div className="plfl" style={{width: "110px"}}>
					<CopyToClipboard onCopy={this._onCopyUrl} text={this._getUrl()}>
						<Button>复制链接</Button>
					</CopyToClipboard>
				</div>
			</div>
		)

	}
}



