import HttpUtil from '../component/HttpUtil'

export default class TaskCommand {

	static pause(ids) {
		var url = 'api/pausetask';
		var param = "ids=" + JSON.stringify(ids)
		HttpUtil.post(url, param, ()=> {
		}, 10000);
	}

	static resume(ids) {
		var url = 'api/resumetask';
		var param = "ids=" + JSON.stringify(ids)
		HttpUtil.post(url, param, ()=> {
		}, 10000);
	}

	static delete(ids, deleteFile) {
		var url = 'api/deletetask';
		var param = "ids=" + JSON.stringify(ids) + "&delfile=" + (deleteFile ? 1 : 0);
		HttpUtil.post(url, param, ()=> {
		}, 10000);
	}
}


