import React, { Component } from 'react';

export default class Utils extends React.Component {

	static size2String(size, base, unit) {
		var s;
		var s1;
		s = parseInt(size / base) + ".";
		s1 = parseInt(((size % base) * 10) / base);
		return s + s1 + unit;
	}

	static formatSize(size) {
		if (size < 1024) 
			return size + "B";
		if (size > (1024 * 1024 * 1024 * 1024))
			return Utils.size2String(size, (1024 * 1024 * 1024 * 1024), 'TB');
		if (size > (1024 * 1024 * 1024))
			return Utils.size2String(size, (1024 * 1024 * 1024), 'GB');
		if (size > (1024 * 1024))
			return Utils.size2String(size, (1024 * 1024), 'MB');
		return Utils.size2String(size, 1024, 'KB');
	}
}




