import React, { Component } from 'react';

function HttpHandler() {
    this.xmlHttpRequest = null;
    this.timerId = null;
    this.cb = null;
    this.canceled = false;
}

HttpHandler.prototype.get = function(url, timeout, cb) {
    if (timeout <= 50 || timeout === null ||　timeout === undefined)
        timeout = 20000;
    this.xmlHttpRequest = new XMLHttpRequest();
    var myself = this;
    this.cb = cb;
    this.xmlHttpRequest.onreadystatechange = function() {
        myself.dealStateChange();
    };
    this.xmlHttpRequest.open('GET', url, true);
    this.xmlHttpRequest.send();
    this.timerId = setTimeout(function() {
        myself.dealTimeout();
    }, timeout);
};

HttpHandler.prototype.post = function(url, param, timeout, cb) {
    if (timeout <= 50 || timeout === null ||　timeout === undefined)
        timeout = 20000;
    this.xmlHttpRequest = new XMLHttpRequest();
    var myself = this;
    this.cb = cb;
    this.xmlHttpRequest.onreadystatechange = function() {
        myself.dealStateChange();
    };
	this.xmlHttpRequest.open('POST', url, true);
	this.xmlHttpRequest.setRequestHeader("Content-Type", "application/x-www-form-urlencoded"); 
    this.xmlHttpRequest.send(param);
    this.timerId = setTimeout(function() {
        myself.dealTimeout();
    }, timeout);
};

HttpHandler.prototype.dealStateChange = function() {
    if (this.xmlHttpRequest === null)
        return;

    if (this.xmlHttpRequest.readyState === 4) {
        var text = this.xmlHttpRequest.responseText;
        if (this.timerId !== null) {
            clearTimeout(this.timerId);
            this.timerId = null;
        }
        if (this.xmlHttpRequest.status === 200) {
            if (this.cb !== null && this.cb !== undefined)
                this.cb(true, text);
        } else {
            if (this.cb !== null && this.cb !== undefined)
                this.cb(false, null);
        }
        this.xmlHttpRequest = null;
    }
};

HttpHandler.prototype.dealTimeout = function() {
    this.abort();
    if (this.cb !== null && this.cb !== undefined)
        this.cb(false, null);
};

HttpHandler.prototype.abort = function() {
    if (this.xmlHttpRequest !== null) {
        this.xmlHttpRequest.abort();
        this.xmlHttpRequest = null;
    }
    if (this.timerId !== null) {
        clearTimeout(this.timerId);
        this.timerId = null;
    }
    this.canceled = true;
};

export default class HttpUtil extends React.Component {
    static get(url, callback, to) {
        var handler = new HttpHandler();
        handler.get(url, to, callback);
        return handler;
	}
	
	static post(url, param, callback, to) {
        var handler = new HttpHandler();
        handler.post(url, param, to, callback);
        return handler;
    }
}


