import React, { Component } from 'react';
import './Publist.css';
import '../component/common.css';
import Constants from '../constants/Constants'
import HttpUtil from '../component/HttpUtil'
import PublistItem from './PublishItem'
import IconButton from '../component/IconButton'
import EventBus from '../component/EventBus'
import InfiniteScroll from 'react-infinite-scroller';
import { List, Spin, Input } from 'antd';
const Search = Input.Search;

export default class Publist extends Component {

	constructor(props) {
		super(props);
		this.state = {
			data: [],
			loading: false,
			listHeight: window.innerHeight - Constants.HEADER_HEIGHT - 50
		}

		console.log("create publist");

		this.queryHandle = null;
	}

	componentDidMount() {
		window.addEventListener('resize', this.handleResize);
		this._queryPublist();
	}

	handleResize = (e) => {
		this.setState({ listHeight: window.innerHeight - Constants.HEADER_HEIGHT - 50 })
	};

	componentWillUnmount() {
		if (this.queryHandle != null) {
			this.queryHandle.abort();
			this.queryHandle = null;
		}
	}

	_searchPublish = (val) => {
		if (val.length === 0)
			return;
		var searchUrl = Constants.PUBSEARCHURL + encodeURIComponent(val);
		var url = Constants.PROXYAPI + encodeURIComponent(searchUrl);
		this._getPublish(url);
	}

	_queryPublist = () => {
		var url = Constants.PROXYAPI + encodeURIComponent(Constants.PUBLISHURL);
		this._getPublish(url);
	}

	_getPublish = (url) => {
		if (this.queryHandle != null) {
			this.queryHandle.abort();
			this.queryHandle = null;
		}
		this.setState({ data: [], loading: true });

		this.queryHandle = HttpUtil.get(url, (result, text) => {
			this.queryHandle = null;
			try {
				var infos = JSON.parse(text);
				for (var i = 0; i < infos.length; i++) {
					infos[i].idx = i;
				}
				this.setState({ loading: false, data: infos });
			} catch (err) {
				this.setState({ loading: false });
				console.log(err);
			}
		}, 15000);
	}

	_onSelChange = (idx, selected) => {
		var newdata = this.state.data;
		newdata[idx].selected = selected;
		this.setState({
			data: newdata
		})
	}

	_renderItem = (item) => {
		return (
			<PublistItem selected={true} fileInfo={item} onSelChange={this._onSelChange}></PublistItem>
		);
	};

	_selectAll = () => {
		var newdata = this.state.data;
		if (newdata == null)
			return;
		for (var i = 0; i < newdata.length; i++) {
			newdata[i].selected = true;
		}
		this.setState({
			data: newdata
		});
	}

	_reverseSelectAll = () => {
		var newdata = this.state.data;
		if (newdata == null)
			return;
		for (var i = 0; i < newdata.length; i++) {
			if (newdata[i].selected == undefined)
				newdata[i].selected = true;
			else
				newdata[i].selected = !newdata[i].selected;
		}
		this.setState({
			data: newdata
		});
	}

	_loadMoreList() {
	}

	_readerControls = () => {
		if (this.state.loading) {
			return (
				<div className="plloading">
					<Spin size="large" className="plspin" />
				</div>
			);
		}

		return (
			<div style={{ overflow: "auto", width: "100%", height: this.state.listHeight }}>
				<InfiniteScroll
					initialLoad={false}
					pageStart={0}
					hasMore={false}
					loadMore={this._loadMoreList}
					useWindow={false}>
					<List
						dataSource={this.state.data}
						renderItem={this._renderItem}
					>
					</List>
				</InfiniteScroll>
			</div>
		);
	}

	_downloadSelect = () => {
		var param = [];
		var data = this.state.data;
		if (data == null)
			return;
		for (var i = 0; i < data.length; i++) {
			if (data[i].selected != true)
				continue
			var url = "yyets://H=" + data[i].fileid + "|S=" + data[i].file_size + "|N=" + data[i].file_name + "|";
			param[param.length] = url;
		}
		if (param.length == 0)
			return;
		EventBus.emitEvent("newtask", param);
	}

	render() {
		/*div className="toolbarIcon">
						<IconButton name="file-add" tip="批量复制"  ></IconButton>
					</div>*/
		return (
			<div>
				<div className="pltoolbar">
					<div className="toolbarIcon">
						<IconButton name="reload" tip="刷新" onClick={this._queryPublist} ></IconButton>
					</div>
					<div className="toolbarIcon">
						<IconButton name="download" tip="下载" onClick={this._downloadSelect}></IconButton>
					</div>
					<div className="toolbarIcon">
						<IconButton name="check-square-o" tip="全选" onClick={this._selectAll} ></IconButton>
					</div>
					<div className="toolbarIcon">
						<IconButton name="check-square" tip="反选" onClick={this._reverseSelectAll}></IconButton>
					</div>
					<div className="plsearch">
						<Search
							placeholder="搜索文件"
							onSearch={value => this._searchPublish(value)}
							style={{ width: 300 }}
						/>
					</div>
				</div>
				{this._readerControls()}
			</div>
		)
	}
}



