import React, { Component } from 'react';
import Utils from '../component/Utils'
import PropTypes from 'prop-types';
import './TaskElement.css';
import TaskIconButton from '../component/TaskIconButton'
import TaskCommand from './TaskCommand'
import EventBus from '../component/EventBus'
import { Progress } from 'antd';

export default class TaskElement extends Component {

	static propTypes = {
		onItemClick: PropTypes.func
	}

	constructor(props) {
		super(props);
	}

	componentDidMount() {
	}

	_renderTaskState = () => {
		return (
			<div className="taskstate"></div>
		);
	}

	_getStateStr = () => {
		switch (this.props.task.state) {
			case 1:
				return "等待下载";
			case 2:
				return "正在下载";
			case 3:
				return "已暂停";
			case 4:
				return "正在停止";
			case 5:
				return "已完成";
			case 6:
				return "已停止";
		}
	}

	_getStateColor = () => {
		switch (this.props.task.state) {
			case 1:
				return "#f1c40f";
			case 2:
				return "#2ecc71";
			case 3:
				return "#95a5a6";
			case 4:
				return "#f1c40f";
			case 5:
				return "#3498db";
			case 6:
				return "#f1c40f";
		}
	}

	_formatSize = () => {
		return Utils.formatSize(this.props.task.file_size) + "/" + Utils.formatSize(this.props.task.finish_size);
	}

	_renderTaskInfo = () => {
		return (
			<div className="taskinfo2">
				<div className="taskinfoem">{this._formatSize()}</div>
				<div className="taskinfoem">{this._getStateStr()}</div>
				<div className="taskinfoem">{Utils.formatSize(this.props.task.down_speed)}/s</div>
				<div className="taskinfoem">{this.props.task.create_time}</div>
			</div>
		);
	}

	_onStartPauseBtn = () => {
		var ids = [];
		ids[0] = this.props.task.file_id;
		if (this.props.task.state == 2 || this.props.task.state == 1) {
			TaskCommand.pause(ids);
		} else {
			TaskCommand.resume(ids);
		}
	}

	_onDeleteTask = () => {
		var params = {};
		params.deleteFile = (this.props.taskType == 1);
		params.ids = [];
		params.ids[0] = this.props.task.file_id;
		EventBus.emitEvent("deltask", params);
	}

	_getFinishedPercent = () => {
		var percent = parseInt(this.props.task.finish_size * 100 / this.props.task.file_size);
		return percent;
	}

	_onClickItem = () => {
		if (this.props.onItemClick == undefined)
			return;
		this.props.onItemClick(this.props.task.index);
	}

	_renderTaskIcon = () => {
		if (this.props.taskType == 1) {
			return (
				<div className="taskicons">
					<div className="taskicon">
						<TaskIconButton fontSize={22} name={this.props.task.state == 2 || this.props.task.state == 1 ? "pause-circle-o" : "download"} tip="继续/暂停" onClick={this._onStartPauseBtn} ></TaskIconButton>
					</div>
					<div className="taskicon">
						<TaskIconButton fontSize={22} name="delete" tip="删除" onClick={this._onDeleteTask} ></TaskIconButton>
					</div>
				</div>
			);
		} else {
			return (
				<div className="taskicons">
					<div className="taskicon">
						<TaskIconButton fontSize={22} name="delete" tip="删除" onClick={this._onDeleteTask} ></TaskIconButton>
					</div>
				</div>
			);
		}
	}

	render() {
		return (
			<div className={this.props.task.selected == true ? "taskem taskfocus" : "taskem taskunfocus"} onClick={this._onClickItem}>
				<div className="taskstate" style={{ backgroundColor: this._getStateColor() }}></div>
				<div>
					<div className="taskinfo">
						<div className="taskinfo1">
							{this.props.task.file_name}
						</div>
						{this._renderTaskInfo()}
					</div>
					{this._renderTaskIcon()}
				</div>
				<div className="taskproc">
					<Progress percent={this._getFinishedPercent()} showInfo={false} strokeWidth={4} />
				</div>
			</div>
		)
	}
}



